import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import SvgButtons from './buttons'

function About(props) {
    
  return <div className="panel"><div className="blue1"><div className="short"><h1>Who?</h1> <h4>Programmer in progress. Currently studying Computer science at Stanford University</h4></div></div><div className="blue2"><div className="short"><h1>Why?</h1><h4>I have been programming since I was 13. I am very passionate about computer science, both because it is an incredibly powerful creative tool that has application in almost every field and because coding is just fun.</h4></div></div><div className="blue3"><div className="short"><h1>What?</h1> <h4>My first language was C++, which I programmed in for several years before branching into web development. Since then, I've mastered JavaScript, HTML, and CSS, as well as various JavaScript libraries like jQuery, Vue.js, and React (this site was made with React)! I've also explored the new hot topic, deep learning, mainly through Google's TensorFlow library. For relevant coursework and links to past projects, see the coding section (top left)</h4></div></div><div className="blue4"><div className="short"><h1>More?</h1><h4>Outside of computer science, I am very interested in biology and neuroscience, which has been reflected by my coursework and summer activities. In my free time, I like to design and code web projects, create digital art, teach myself new skills (both programming-related and otherwise), and force my dog to spend time with me. In the (near) future I hope to branch into app development and gain more practical experience with various various machine learning apis.</h4></div></div></div>;
}
function Code(props) {
  return <h1>Hello, {props.name}</h1>;
}
function Contact(props) {
  return <div className="panel"><div className="blue1"><div className="short"><h1>Reach Me At</h1><div className="flex"><div className="flex-item"><a target="_blank" href="https://www.facebook.com/gabrielle.cohn.94"><img src="facebook.png"/></a></div><div className="flex-item"><img src="email.png"/></div><div className="flex-item"><a target="_blank" href="https://github.com/gollyzoom"><img src="github.png"/></a></div></div></div></div></div>;
}

const SvgComponent = props => (
  <svg width={320} height={320} fill="none" {...props}>
    <circle cx={160} cy={160} r={160} fill="#0582CA" />
    <circle cx={160} cy={160} r={140} fill="#006494" />
    <circle cx={160.5} cy={160.5} r={122.5} fill="#003554" />
    <circle cx={160.5} cy={160.5} r={102.5} fill="#C4C4C4" />
    <circle cx={160.5} cy={160.5} r={102.5} fill="url(#prefix__pattern0)" />
    <defs>
      <pattern
        id="prefix__pattern0"
        patternContentUnits="objectBoundingBox"
        width={1}
        height={1}
      >
        <use
          xlinkHref="#prefix__image0"
          transform="matrix(.0004 0 0 .0004 0 -.167)"
        />
      </pattern>
      <image
        id="prefix__image0"
        width={2548}
        height={3364}
        x="25" y="25"
        xlinkHref="profile.jpg"
      />
    </defs>
  </svg>
)

class App extends Component {
    constructor(props) {
        super(props);
        this.handleClick = this.handleClick.bind(this);
        this.state = {mode:"about"};
    }
    handleClick(button){
        console.log("clicked " + button);
        this.setState({
          mode: button
        });
    }
  render() {
    const mode = this.state.mode;
    let panel;
    if (mode == "code") {
      panel = <Code></Code>;
    } 
    else if(mode == "about") {
      panel = <About></About>;
    }
    else if(mode == "contact") {
      panel = <Contact></Contact>;
    }
    return (
      <div className="App">
        <div id="profile">
            <SvgComponent></SvgComponent>
        </div>
        <div id="buttons">
            <SvgButtons handleClick = {this.handleClick}></SvgButtons>
        </div>
        <h1>Gabrielle Cohn</h1>
        {panel}
      </div>
    );
  }
}
export default App;

